.entire-page {
  display: grid;
  height: 100%;
  min-height: 90vh;
  padding: 0px 5vw 30px 5vw;
  grid-template-columns: 1fr;
  grid-template-rows: 150px auto;
  grid-template-areas:
    "title"
    "body";
}

.login-divider {
  display: flex;
  align-items: center;
  justify-content: center;
}

.login-divider-line {
  width: 88px;
  height: 3px;
  background-color: white;
  margin: 5px 10px;
}

.page-content-container {
  grid-area: body;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.diet-row {
  display: flex;
  flex-direction: row;
  justify-content: center;

  margin: 10px;
}
